import { createGlobalStyle } from "styled-components";

export default createGlobalStyle`
  * {
    margin: 0;
    padding: 0;
    box-sizing:border-box;

    color: var(--white);
  }
  html, body, #root {
    max-width: 100vw;
    max-height: 100vh;
    width: 100%;
    height: 100%;
  }
  button, a {
    cursor: pointer;
  }
  *, button, input {
    border: 0;
    outline: 0;
    background: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  ul,li {
    list-style: none;
  }
  html {
    background: var(--primary);
  }
  :root {
    --primary: #000;
    --secondary: #15181C;
    --search: #202327;
    --white: #D9D9D9;
    --gray: #7A7A7A;
    --outline: #2F3336;
    --retweet: #00C06B;
    --like: #E8265E;
    --twitter: #33A1F2;
    --twitter-dark-hover: #011017;
    --twitter-light-hover: #2C8ED6;
  }
  :root {
    --primary: #fff;
    --secondary: #15181C;
    --search: #202327;
    --white: #333;
    --gray: #999;
    --outline: #f3f3f3;
    --retweet: #00C06B;
    --like: #E8265E;
    --twitter: #444;
    --twitter-dark-hover: #fff;
    --twitter-light-hover: #2C8ED6;
  }
`;
