import React from "react";

import {
  Container,
  Body,
  Avatar,
  Content,
  Header,
  Dot,
  Description,
  ImageContent,
  Icons,
  Status,
  CommentIcon,
  ShareIcon,
  LikeIcon,
} from "./styles";

const Card = () => {
  return (
    <Container>
      <Body>
        <Avatar />
        <Content>
          <Header>
            <strong>name</strong>
            <span>@gamil</span>
            <Dot />
            <time>1h</time>
          </Header>
          <Description>
            Net zero carbon by 2030... Sky Sports will highlight the impact of
            climate change at some of the UK's highest-profile sporting events
            this summer, ahead of the UK hosting the United Nations Climate
            Change Conference in November
          </Description>
          <ImageContent />
          <Icons>
            <Status>
              <CommentIcon />
              00
            </Status>
            <Status>
              <ShareIcon />
              00
            </Status>
            <Status>
              <LikeIcon />
              00
            </Status>
          </Icons>
        </Content>
      </Body>
    </Container>
  );
};

export default Card;
