import React, { useState } from "react";
import ProfilePage from "../ProfilePage";

import {
  Container,
  Header,
  LeftWrapper,
  Theme,
  BackIcon,
  ProfileInfo,
  BottomMenu,
  HomeIcon,
  SearchIcon,
  BellIcon,
  EmailIcon,
} from "./styles";

const Main = () => {
  const [themeState, setThemeState] = useState(true);

  return (
    <Container>
      <Header>
        <LeftWrapper>
          <button>
            <BackIcon />
          </button>

          <ProfileInfo>
            <strong>name</strong>
            <span>99 news</span>
          </ProfileInfo>
        </LeftWrapper>
        <Theme
          outlined
          onClick={() => {
            setThemeState(!themeState);
          }}
        >
          {themeState ? "dark" : "light"}
        </Theme>
      </Header>

      <ProfilePage />

      <BottomMenu>
        <HomeIcon />
        <SearchIcon />
        <BellIcon />
        <EmailIcon />
      </BottomMenu>
    </Container>
  );
};

export default Main;
